/**
 * Console Override í™œì„±í™” ìƒíƒœ í™•ì¸
 */
function isConsoleOverrideEnabled(): boolean {
  try {
    const value = localStorage.getItem('debugMode');
    const result = value === 'true';
    // ë””ë²„ê¹…ìš© ë¡œê·¸ (originalLog ì‚¬ìš©)
    if (typeof (window as any).__debugLoggerInit === 'undefined') {
      (window as any).__debugLoggerInit = true;
      originalLog(`%c[DEBUG] localStorage.debugMode = "${value}" â†’ ${result}`, 'color: #9C27B0; font-weight: bold;');
    }
    return result;
  } catch (e) {
    originalLog(`%c[DEBUG] localStorage ì ‘ê·¼ ì‹¤íŒ¨: ${e}`, 'color: #F44336;');
    return false;
  }
}

